:root{
  --X-color:#2196f3;
  --O-color:#f44336;
  --green:#4caf50;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  display: grid;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  height: 100vh;
  background-color: #eee;
}
.titulo{
  text-align: center;
  font-size: 3rem;
  color: #555;
  margin-bottom:2rem;
}
.borda{
  display: grid;
  grid-template-columns: repeat(3,240px);
  grid-template-rows: repeat(3,200px);
  gap: 1rem;
  
}
.borda.perdeu{
  opacity: 0.1;
  
  

}
.celula{
  display: grid;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 5px;
  font-size:3rem;
  font-weight:bold;
  color:#00000066;
  cursor: pointer;
  position: relative;
}
.celula.O{
  background-color: var(--O-color);
}
.celula.X{
  background-color: var(--X-color);

}
.celula.O, .celula.X, .perdeu .celula{
  cursor:not-allowed;
}
.celula:not(.O):not(.X):hover{
  box-shadow: 0 20px 25px -5px rgba(0,0,0,0.1), 0  10px 10px -5px rgba(0,0,0,0.04);
}
.mensagem{
 
  text-align: center;
  font-size: 2rem;
  color: #555;
 
}
.mensagem span.O{
  color: var(--O-color);
 
}
.mensagem span.X{
  color:var(--X-color);
 
}
button{
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.1), 0 1px 2px 0 rgba(0,0,0,0.06);
  background-color:var(--green);
  color: white;
  font-size:1.2rem;
  margin-top: 30px;
  cursor: pointer;

}